import { Doughnut } from 'react-chartjs-2';
import PropTypes from 'prop-types';
import {
  Box,
  Card,
  CardContent,
  CardHeader,
  Divider,
  Typography,
  colors,
  useTheme
} from '@material-ui/core';
// import LaptopMacIcon from '@material-ui/icons/LaptopMac';
// import PhoneIcon from '@material-ui/icons/Phone';
// import TabletIcon from '@material-ui/icons/Tablet';
import React, { useState, useEffect } from 'react';
import Axios from 'axios';

const TrafficByDevice = (...props) => {
  const theme = useTheme();
  const [items, setItems] = useState([]);
  useEffect(() => {
    Axios.get('https://3zmmnjf896.execute-api.us-east-2.amazonaws.com/dev/getgraph')
      .then((res) => {
        setItems(res.data);
      });
  }, []);
  console.log('noddy1', props);
  let data1;
  if (items.length > 0) {
    const data = {
      datasets: [
        {
          data: items[4],
          backgroundColor: [
            colors.indigo[500],
            colors.red[600],
            colors.orange[600]
          ],
          borderWidth: 8,
          borderColor: colors.common.white,
          hoverBorderColor: colors.common.white
        }
      ],
      labels: ['Mental', 'Posture', 'Physical']
    };
    data1 = data;
  }
  const options = {
    animation: false,
    cutoutPercentage: 80,
    layout: { padding: 0 },
    legend: {
      display: false
    },
    maintainAspectRatio: false,
    responsive: true,
    tooltips: {
      backgroundColor: theme.palette.background.paper,
      bodyFontColor: theme.palette.text.secondary,
      borderColor: theme.palette.divider,
      borderWidth: 1,
      enabled: true,
      footerFontColor: theme.palette.text.secondary,
      intersect: false,
      mode: 'index',
      titleFontColor: theme.palette.text.primary
    }
  };
  let devices1;
  if (items.length > 0) {
    const devices = [
      {
        title: 'Mental',
        value: items[4][0],
        color: colors.indigo[500]
      },
      {
        title: 'Posture',
        value: items[4][1],
        color: colors.red[600]
      },
      {
        title: 'Physical',
        value: items[4][2],
        color: colors.orange[600]
      }
    ];
    devices1 = devices;
  }

  return (
    <Card {...props}>
      <CardHeader title="Health Pie Chart " />
      <Divider />
      <CardContent>
        <Box
          sx={{
            height: 300,
            position: 'relative'
          }}
        >
          <Doughnut
            data={data1}
            options={options}
          />
        </Box>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'center',
            pt: 2
          }}
        >
          {devices1 && devices1.map(({
            color,
            title,
            value
          }) => (
            <Box
              key={title}
              sx={{
                p: 1,
                textAlign: 'center'
              }}
            >
              <Typography
                color="textPrimary"
                variant="body1"
              >
                {title}
              </Typography>
              <Typography
                style={{ color }}
                variant="h2"
              >
                {value}
                %
              </Typography>
            </Box>
          ))}
        </Box>
      </CardContent>
    </Card>
  );
};
TrafficByDevice.propTypes = {
  items: PropTypes.object
};

export default TrafficByDevice;
